import { useStaticQuery, graphql } from 'gatsby'

/**
 * Query tags by articles an return them as a flat array
  */
export default function useTagsByArticles() {
  const { allMarkdownRemark } = useStaticQuery(graphql`
    query allTagsByArticles {
      allMarkdownRemark(
          filter: { 
              fileAbsolutePath: { regex: "/(articles)/.*\\.md$/" }
              frontmatter: { published: { ne: "No" } }
          }
      ) {
        group(field: frontmatter___tags) {
          tag: fieldValue
        }
      }
    }
  `)

  /**
   * Return a flat array of tags
   */
  return allMarkdownRemark.group.map(tag => tag.tag)
}
